import React from "react";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ReferenceLine,
} from "recharts";
import Data from "../data/data";
import "../styles/SimpleBarChart.scss";

export default function SimpleBarChart() {
  let newDataArray = [];
  let sessions = Data.USER_ACTIVITY[0].sessions;
  sessions.forEach((element, index) => {
    const newObject = {
      name: index + 1,
      kCal: element.calories,
      kg: element.kilogram,
    };
    newDataArray.push(newObject);
  });

  const CustomTooltip = ({ active, payload, label }: any) => {
    if (active && payload && payload.length) {
      return (
        <div className="barchart-tooltip">
          <p className="label">{`${payload[0].value}kg`}</p>
          <p className="label">{`${payload[1].value}kCal`}</p>
        </div>
      );
    }
    return null;
  };

  return (
    <BarChart
      width={850}
      height={320}
      barGap={-40}
      maxBarSize={9}
      data={newDataArray}
      margin={{
        top: 15,
        right: 60,
        left: 35,
        bottom: 50,
      }}
    >
      <CartesianGrid strokeDasharray="2 3" vertical={false} stroke="#DEDEDE" />
      <XAxis dataKey="name" tickSize="0" tickMargin="30" stroke="#9B9EAC" />
      <YAxis
        dataKey="kg"
        orientation="right"
        tickSize="0"
        tickMargin="25"
        stroke="#9B9EAC"
        type="number"
        domain={["dataMin - 5", "dataMax + 5"]}
      />
      <ReferenceLine y={0} stroke="#DEDEDE" />
      <Tooltip content={CustomTooltip} />
      <Legend
        align="right"
        verticalAlign="top"
        iconType="circle"
        iconSize={8}
      />
      <Bar name="Poids (kg)" dataKey="kg" fill="black" />
      <Bar name="Calories br√ªl√©es (kCal)" dataKey="kCal" fill="red" />
    </BarChart>
  );
}
