import React from "react";
import { PieChart, Pie, Cell, Legend } from "recharts";
import "../styles/CustomPie.scss";

const data = [
  { name: "Group A", value: 60 },
  { name: "Group B", value: 40 },
];
const COLORS = ["red", "white"];

export default function CustomPie() {
  const renderLegend = () => {
    return <p className="piechart-legend">{"Score"}</p>;
  };

  return (
    <PieChart width={260} height={270}>
      <text x={80} y={160} fill="black" fontSize="10">
        {data[0].value + "% de votre objectif"}
      </text>
      <Legend align="left" verticalAlign="top" content={renderLegend} />
      <Pie
        data={data}
        cx={125}
        cy={100}
        innerRadius={90}
        outerRadius={105}
        startAngle={90}
        endAngle={450}
        fill="#8884d8"
        paddingAngle={0}
        dataKey="value"
        label={false}
      >
        {data.map((entry, index) => (
          <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
        ))}
      </Pie>
    </PieChart>
  );
}
